import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

import '../styles/header.scss';

const IMG_URL = 'https://d3i4yxtzktqr9n.cloudfront.net/web-eats-v2';

const Header = () => {
  const [initialFields, setInitialFields]
    = useState({ location: localStorage.getItem('location') || 'London', search: localStorage.getItem('search') || '' });

  const [fields, setFields] = useState(initialFields);

  const [editing, setEditing] = useState({ location: false, search: false });

  const inputRef = useRef();

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    if (editing.location) {
      inputRef.current.location.focus();
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  });

  const handleClick = (event) => {
    const { name } = event.target;

    setEditing({ ...editing, [name]: true });
  };

  const handleChange = (event) => {
    const { name, value } = event.target;

    setFields({ ...fields, [name]: value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const { value, name } = event.target;

    if (value) {
      localStorage.setItem(name, value);

      setInitialFields(localStorage.getItem('location'));
      setEditing({ ...editing, location: false });
    }
  };

  const handleClickOutside = (event) => {
    const { name } = event.target;

    if (inputRef.current && !inputRef.current.contains(event.target)) {
      setInitialFields({ ...initialFields, [name]: initialFields[name] });
      setEditing(false);
    }
  };

  return (
    <header className="header">
      <div className="container header__container">
        <Link to="/">
          <img
            src={`${IMG_URL}/f8f0721f871b3704cce92eb96bc6e504.svg`}
            alt="Uber Eats"
          />
        </Link>

        {
          editing.location
          && (
            <form
              action=""
              name="location"
              onSubmit={event => handleSubmit(event)}
              ref={inputRef}
              className="header__location header__location-form"
            >
              <input
                type="text"
                name="location"
                value={fields.location}
                onChange={event => handleChange(event)}
              />
            </form>
          )
        }

        {
          editing.location
          || (
            <button
              type="button"
              name="location"
              onClick={event => handleClick(event)}
              className="header__location"
            >
              {initialFields.location}
            </button>
          )
        }

        <button type="button" className="header__delivery">Deliver now</button>

        {
          editing.search
          && (
            <form
              action=""
              onSubmit={event => handleSubmit(event)}
              ref={inputRef}
            >
              <input
                type="text"
                name="search"
                value={fields.search}
                onChange={event => handleChange(event)}
              />
            </form>
          )
        }
        {
          editing.search
          || (
            <button
              type="button"
              name="search"
              className="header__search"
              onClick={event => handleClick(event)}
            >
              Search
            </button>
          )
        }

        <a href="#" className="header__sign-in">Sign in</a>

      </div>
    </header>
  );
};

export default Header;
